import React from 'react';
import { graphql } from 'gatsby';
import { SEO } from 'src/components';
import AboutMDX from 'src/internal/about.mdx';
import { baseStyle } from 'src/styles';
import { AboutQuery } from '../../types/graphql-types';

import styled from '@emotion/styled';

type Prop = {
  className?: string;
  data: AboutQuery;
};

const Component: React.FC<Prop> = ({ className, data }) => (
  <main className={className}>
    <h1>About</h1>
    <AboutMDX />
  </main>
);

const StyledComponent = styled(Component)`
  ${baseStyle}
  @media screen and (max-width: 1100px) {
  }
  @media screen and (max-width: 768px) {
  }
  @media screen and (max-width: 480px) {
  }
  @media screen and (max-height: 430px) {
  }
`;

// export default ({ path, data }: { path: string; data: AboutQuery }) => (
//   <>
//     <SEO title='about' pathname={path} />
//     <StyledComponent />
//   </>
// );
export default ({ path, data }: { path: string; data: AboutQuery }) => (
  <>
    <SEO title='about' pathname={path} />
    <StyledComponent data={data} />
  </>
);

export const query = graphql`
  query About {
    allMdx(filter: { fields: { collection: { eq: "about" } } }) {
      nodes {
        fields {
          collection
        }
      }
    }
  }
`;
